import React, {
  useRef,
  useCallback,
  useEffect,
  useReducer,
  useContext,
  useMemo,
} from 'react';
import Sendbird from 'sendbird';
import * as utils from './utils';
import './index.scss';

import MessageInputWrapper from './components/MessageInpuetWrapper';
import FrozenChannelNotification from './components/FrozenNotification';
import OpenchannelConversationHeader from '../../ui/OpenchannelConversationHeader';
import OpenchannelConversationScroll from './components/OpenchannelConversationScroll';
import PlaceHolder, { PlaceHolderTypes } from '../../ui/PlaceHolder';
import { UserProfileProvider } from '../../lib/UserProfileContext';

import {
  EveryMessage,
  ClientUserMessage,
  ClientFileMessage,
  OpenChannelProps,
} from '../../index';
import messagesReducer from './dux/reducers';
import messagesInitialState from './dux/initialState';
import * as messageActionTypes from './dux/actionTypes';
import withSendbirdContext from '../../lib/SendbirdSdkContext.jsx';
import { LocalizationContext } from '../../lib/LocalizationContext';
import { scrollIntoLast } from './utils';
import * as topics from '../../lib/pubSub/topics';
import uuidv4 from '../../utils/uuid';

const COMPONENT_CLASS_NAME = 'sendbird-openchannel-conversation';

interface Props extends OpenChannelProps {
  stores: {
    sdkStore?: SendbirdUIKit.SdkStore,
    userStore?: {
      user: Sendbird.User,
    },
  };
  config: {
    userId: string,
    isOnline: boolean,
    logger?: SendbirdUIKit.Logger,
    theme?: string,
    // @ts-ignore: Unreachable code error
    pubSub: any;
    disableUserProfile?: boolean;
    renderUserProfile?(): JSX.Element;
  };
}

export const OpenchannelConversation = (props: Props): JSX.Element => {
  // props
  const {
    useMessageGrouping,
    channelUrl,
    stores,
    config,
    queries = {},
    disableUserProfile,
    fetchingParticipants = false, // We didn't decide to support fetching participant list
    renderCustomMessage,
    renderUserProfile,
    renderChannelTitle,
    renderMessageInput,
    onBeforeSendUserMessage,
    onBeforeSendFileMessage,
    onChatHeaderActionClick
  } = props;

  const {
    sdkStore,
    userStore,
  } = stores;
  const {
    userId,
    isOnline,
    logger,
    pubSub,
  } = config;

  const {
    sdk
  } = sdkStore;
  const {
    user
  } = userStore;

  // hook variables
  const [messagesStore, messagesDispatcher] = useReducer(messagesReducer, messagesInitialState);
  const {
    allMessages,
    loading,
    initialized,
    currentOpenChannel,
    isInvalid,
    hasMore,
    lastMessageTimestamp,
    operators,
    bannedParticipantIds,
    mutedParticipantIds,
  } = messagesStore;
  // ref
  const messageInputRef = useRef(null); // useSendMessageCallback
  const conversationScrollRef = useRef(null); // useScrollAfterSendMessageCallback
  const { stringSet } = useContext(LocalizationContext);

  // const
  const sdkInit = sdkStore.initialized;
  const userFilledMessageListParams = queries ? queries.messageListParams: null;
  const disabled = !initialized
    || !isOnline
    || utils.isDisabledBecauseFrozen(currentOpenChannel, userId);
  // || utils.isDisabledBecauseMuted(mutedParticipantIds, userId)
  const userDefinedDisableUserProfile = disableUserProfile || config.disableUserProfile;
  const userDefinedRenderProfile = renderUserProfile || config.renderUserProfile;

  // useMemo
  const amIBanned = useMemo(() => {
    return bannedParticipantIds.indexOf(user.userId) >= 0;
  }, [channelUrl, bannedParticipantIds, user]);
  const amIMuted = useMemo(() => {
    return mutedParticipantIds.indexOf(user.userId) >= 0;
  }, [channelUrl, mutedParticipantIds, user]);
  const amIOperator = useMemo(() => {
    return operators.map(operator => operator.userId).indexOf(user.userId) >= 0;
  }, [channelUrl, operators, user]);

  // useSetChannel
  useEffect(() => {
    if (channelUrl && sdkInit && sdk && sdk.OpenChannel) {
      logger.info('OpenChannel | useSetChannel fetching channel', channelUrl);
      sdk.OpenChannel.getChannel(channelUrl, (openChannel, error) => {
        if (!error) {
          logger.info('OpenChannel | useSetChannel fetched channel', openChannel);
          messagesDispatcher({
            type: messageActionTypes.SET_CURRENT_CHANNEL,
            payload: openChannel,
          });
          openChannel.enter((response, error) => {
            if (error) {
              logger.warning('OpenChannel | useSetChannel enter channel failed', { channelUrl, error });
              messagesDispatcher({
                type: messageActionTypes.SET_CHANNEL_INVALID,
              });
            }
            if (fetchingParticipants) {
              // fetch participants, banned participantIds, muted participantIds
              const participantListQuery = openChannel.createParticipantListQuery();
              const bannedParticipantListQuery = openChannel.createBannedUserListQuery();
              const mutedParticipantListQuery = openChannel.createMutedUserListQuery();
              utils.fetchWithListQuery(
                participantListQuery,
                logger,
                (users) => {
                  messagesDispatcher({
                    type: messageActionTypes.FETCH_PARTICIPANT_LIST,
                    payload: {
                      channel: openChannel,
                      users,
                    },
                  });
                },
              );
              utils.fetchWithListQuery(
                bannedParticipantListQuery,
                logger,
                (users) => {
                  messagesDispatcher({
                    type: messageActionTypes.FETCH_BANNED_USER_LIST,
                    payload: {
                      channel: openChannel,
                      users,
                    },
                  });
                },
              );
              utils.fetchWithListQuery(
                mutedParticipantListQuery,
                logger,
                (users) => {
                  messagesDispatcher({
                    type: messageActionTypes.FETCH_MUTED_USER_LIST,
                    payload: {
                      channel: openChannel,
                      users,
                    },
                  });
                },
              );
            }
          });
        } else {
          logger.warning('OpenChannel | useSetChannel fetch channel failed', { channelUrl, error });
          messagesDispatcher({
            type: messageActionTypes.SET_CHANNEL_INVALID,
          });
        }
      });
      // .then((openChannel) => {
      //   logger.info('OpenChannel | useSetChannel fetched channel', openChannel);
      //   messagesDispatcher({
      //     type: messageActionTypes.SET_CURRENT_CHANNEL,
      //     payload: openChannel,
      //   });
      // })
      // .catch((error) => {
      //   logger.warning('OpenChannel | useSetChannel fetch channel failed', { channelUrl, error });
      //   messagesDispatcher({
      //     type: messageActionTypes.SET_CHANNEL_INVALID,
      //   });
      // });
    }
  }, [channelUrl, sdkInit]);

  // handles API calls from withSendbird
  useEffect(() => {
    const subscriber = new Map();
    if (!pubSub || !pubSub.subscribe) {
      return;
    }
    subscriber.set(topics.SEND_USER_MESSAGE, pubSub.subscribe(topics.SEND_USER_MESSAGE, (msg) => {
      const { channel, message } = msg;
      scrollIntoLast('.sendbird-msg--scroll-ref');
      if (channel && (channelUrl === channel.url)) {
        messagesDispatcher({
          type: messageActionTypes.SENDING_MESSAGE_SUCCEEDED,
          payload: message,
        });
      }
    }));
    subscriber.set(topics.SEND_MESSAGE_START, pubSub.subscribe(topics.SEND_MESSAGE_START, (msg) => {
      const { channel, message } = msg;
      if (channel && (channelUrl === channel.url)) {
        messagesDispatcher({
          type: messageActionTypes.SENDING_MESSAGE_START,
          payload: message,
        });
      }
    }));
    subscriber.set(topics.SEND_FILE_MESSAGE, pubSub.subscribe(topics.SEND_FILE_MESSAGE, (msg) => {
      const { channel, message } = msg;
      scrollIntoLast('.sendbird-msg--scroll-ref');
      if (channel && (channelUrl === channel.url)) {
        messagesDispatcher({
          type: messageActionTypes.SENDING_MESSAGE_SUCCEEDED,
          payload: message,
        });
      }
    }));
    subscriber.set(topics.UPDATE_USER_MESSAGE, pubSub.subscribe(topics.UPDATE_USER_MESSAGE, (msg) => {
      const { channel, message, fromSelector } = msg;
      if (fromSelector && channel && (channelUrl === channel.url)) {
        messagesDispatcher({
          type: messageActionTypes.ON_MESSAGE_UPDATED,
          payload: { channel, message },
        });
      }
    }));
    subscriber.set(topics.DELETE_MESSAGE, pubSub.subscribe(topics.DELETE_MESSAGE, (msg) => {
      const { channel, messageId } = msg;
      if (channel && (channelUrl === channel.url)) {
        messagesDispatcher({
          type: messageActionTypes.ON_MESSAGE_DELETED,
          payload: messageId,
        });
      }
    }));

    return () => {
      if (subscriber) {
        subscriber.forEach((s) => {
          try {
            s.remove();
          } catch {
            //
          }
        });
      }
    };
  }, [channelUrl, sdkInit]);

  // useHandleChannelEvents
  useEffect(() => {
    const messageReceiverId = uuidv4();
    if (currentOpenChannel && currentOpenChannel.url && sdk && sdk.ChannelHandler) {
      const ChannelHandler = new sdk.ChannelHandler();
      logger.info('OpenChannel | useHandleChannelEvents: Setup event handler', messageReceiverId);

      ChannelHandler.onMessageReceived = (channel, message) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMessageReceived', { channelUrl, message });
        messagesDispatcher({
          type: messageActionTypes.ON_MESSAGE_RECEIVED,
          payload: { channel, message },
        });
      };
      ChannelHandler.onMessageUpdated = (channel, message) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMessageUpdated', { channelUrl, message });
        messagesDispatcher({
          type: messageActionTypes.ON_MESSAGE_UPDATED,
          payload: { channel, message },
        });
      };
      ChannelHandler.onMessageDeleted = (channel, messageId) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMessageDeleted', { channelUrl, messageId });
        messagesDispatcher({
          type: messageActionTypes.ON_MESSAGE_DELETED,
          payload: { channel, messageId },
        });
      };
      ChannelHandler.onOperatorUpdated = (channel, operators) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onOperatorUpdated', { channelUrl, operators });
        messagesDispatcher({
          type: messageActionTypes.ON_OPERATOR_UPDATED,
          payload: { channel, operators },
        });
      };
      ChannelHandler.onUserEntered = (channel, user) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onUserEntered', { channelUrl, user });
        messagesDispatcher({
          type: messageActionTypes.ON_USER_ENTERED,
          payload: { channel, user },
        });
      };
      ChannelHandler.onUserExited = (channel, user) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onUserExited', { channelUrl, user });
        messagesDispatcher({
          type: messageActionTypes.ON_USER_EXITED,
          payload: { channel, user },
        });
      };
      ChannelHandler.onUserMuted = (channel, user) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onUserMuted', { channelUrl, user });
        messagesDispatcher({
          type: messageActionTypes.ON_USER_MUTED,
          payload: { channel, user },
        });
      };
      ChannelHandler.onUserUnmuted = (channel, user) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onUserUnmuted', { channelUrl, user });
        messagesDispatcher({
          type: messageActionTypes.ON_USER_UNMUTED,
          payload: { channel, user },
        });
      };
      ChannelHandler.onUserBanned = (channel, user) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onUserBanned', { channelUrl, user });
        messagesDispatcher({
          type: messageActionTypes.ON_USER_BANNED,
          payload: { channel, user },
        });
      };
      ChannelHandler.onUserUnbanned = (channel, user) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onUserUnbanned', { channelUrl, user });
        messagesDispatcher({
          type: messageActionTypes.ON_USER_UNBANNED,
          payload: { channel, user },
        });
      };
      ChannelHandler.onChannelFrozen = (channel) => {
        logger.info('OpenChannel | useHandleChannelEvents: onChannelFrozen', channel);
        messagesDispatcher({
          type: messageActionTypes.ON_CHANNEL_FROZEN,
          payload: channel,
        });
      };
      ChannelHandler.onChannelUnfrozen = (channel) => {
        logger.info('OpenChannel | useHandleChannelEvents: onChannelUnfrozen', channel);
        messagesDispatcher({
          type: messageActionTypes.ON_CHANNEL_UNFROZEN,
          payload: channel,
        });
      };
      ChannelHandler.onChannelChanged = (channel) => {
        logger.info('OpenChannel | useHandleChannelEvents: onChannelChanged', channel);
        messagesDispatcher({
          type: messageActionTypes.ON_CHANNEL_CHANGED,
          payload: channel,
        });
      };
      ChannelHandler.onMetaDataCreated = (channel, metaData) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMetaDataCreated', { channelUrl, metaData });
        messagesDispatcher({
          type: messageActionTypes.ON_META_DATA_CREATED,
          payload: { channel, metaData },
        });
      };
      ChannelHandler.onMetaDataUpdated = (channel, metaData) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMetaDataUpdated', { channelUrl, metaData });
        messagesDispatcher({
          type: messageActionTypes.ON_META_DATA_UPDATED,
          payload: { channel, metaData },
        });
      };
      ChannelHandler.onMetaDataDeleted = (channel, metaDataKeys) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMetaDataDeleted', { channelUrl, metaDataKeys });
        messagesDispatcher({
          type: messageActionTypes.ON_META_DATA_DELETED,
          payload: { channel, metaDataKeys },
        });
      };
      ChannelHandler.onMetaCountersCreated = (channel, metaCounter) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMetaCountersCreated', { channelUrl, metaCounter });
        messagesDispatcher({
          type: messageActionTypes.ON_META_COUNTERS_CREATED,
          payload: { channel, metaCounter },
        });
      };
      ChannelHandler.onMetaCountersUpdated = (channel, metaCounter) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMetaCountersUpdated', { channelUrl, metaCounter });
        messagesDispatcher({
          type: messageActionTypes.ON_META_COUNTERS_UPDATED,
          payload: { channel, metaCounter },
        });
      };
      ChannelHandler.onMetaCountersDeleted = (channel, metaCounterKeys) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMetaCountersDeleted', { channelUrl, metaCounterKeys });
        messagesDispatcher({
          type: messageActionTypes.ON_META_COUNTERS_DELETED,
          payload: { channel, metaCounterKeys },
        });
      };
      ChannelHandler.onMentionReceived = (channel, message) => {
        const channelUrl = channel.url;
        logger.info('OpenChannel | useHandleChannelEvents: onMentionReceived', { channelUrl, message });
        messagesDispatcher({
          type: messageActionTypes.ON_MENTION_RECEIVED,
          payload: { channel, message },
        });
      };
      sdk.addChannelHandler(messageReceiverId, ChannelHandler);
    }

    return () => {
      if (sdk && sdk.removeChannelHandler) {
        logger.info('OpenChannel | useHandleChannelEvents: Removing message receiver handler', messageReceiverId);
        sdk.removeChannelHandler(messageReceiverId);
      }
    };
  }, [currentOpenChannel]);

  // useInitialMessagesFetch
  useEffect(() => {
    logger.info('OpenChannel | useInitialMessagesFetch: Setup started', currentOpenChannel);

    messagesDispatcher({
      type: messageActionTypes.RESET_MESSAGES,
    });

    if (sdk && sdk.MessageListParams && currentOpenChannel && currentOpenChannel.getMessagesByTimestamp) {
      const messageListParams = new sdk.MessageListParams();
      messageListParams.prevResultSize = 30;
      messageListParams.isInclusive = true;
      messageListParams.includeReplies = false;
      messageListParams.includeReactions = false;
      if (userFilledMessageListParams) {
        Object.keys(userFilledMessageListParams).forEach(key => {
          messageListParams[key] = userFilledMessageListParams[key];
        });
        logger.info('OpenChannel | Used customizedMessageListParams');
      }

      logger.info('OpenChannel | Fetching messages', { currentOpenChannel, messageListParams });

      currentOpenChannel.getMessagesByTimestamp(
        new Date().getTime(),
        messageListParams,
        (messages, error) => {
          if (!error) {
            logger.info('OpenChannel | Fetching messages succeeded', messages);
            const hasMore = (messages && messages.length > 0);
            const lastMessageTimestamp = hasMore
            ? messages[0].createdAt
            : null;
            messagesDispatcher({
              type: messageActionTypes.GET_PREV_MESSAGES_SUCESS,
              payload: {
                currentOpenChannel,
                messages,
                hasMore,
                lastMessageTimestamp,
              },
            });
            setTimeout(() => {
              utils.scrollIntoLast('.sendbird-msg--scroll-ref');
            }, 500);
          } else {
            logger.error('OpenChannel | Fetching messages failed', error);
            messagesDispatcher({
              type: messageActionTypes.GET_PREV_MESSAGES_FAIL,
              payload: {
                currentOpenChannel,
                messages: [],
                hasMore: false,
                lastMessageTimestamp: 0,
              },
            });
          }
        },
      );
      // todo: use promise after CoreSDK adds required TS methods
      // .then((messages) => {
      //   console.log('get messages via getMessagesByTimestamp', messages);
      //   const hasMore = (messages && messages.length > 0);
      //   const lastMessageTimestamp = hasMore
      //     ? messages[0].createdAt
      //     : null;
      //   messagesDispatcher({
      //     type: messageActionTypes.GET_PREV_MESSAGES_SUCESS,
      //     payload: {
      //       currentOpenChannel,
      //       messages,
      //       hasMore,
      //       lastMessageTimestamp,
      //     },
      //   });
      // })
      // .catch((error: Error): void => {
      //   logger.error('OpenChannel | Fetching messages failed', error);
      //   messagesDispatcher({
      //     type: messageActionTypes.GET_PREV_MESSAGES_FAIL,
      //     payload: {
      //       currentOpenChannel,
      //       messages: [],
      //       hasMore: false,
      //       lastMessageTimestamp: 0,
      //     }
      //   });
      // });
    }
  }, [currentOpenChannel]);

  // useScrollCallback
  const onScroll = useCallback((callback) => {
    if (!hasMore) {
      return;
    }
    const messageListParams = new sdk.MessageListParams();
    messageListParams.prevResultSize = 30;
    messageListParams.includeReplies = false;
    messageListParams.includeReactions = false;

    if (userFilledMessageListParams) {
      Object.keys(userFilledMessageListParams).forEach(key => {
        messageListParams[key] = userFilledMessageListParams[key];
      });
      logger.info('OpenChannel: Used userFilledMessageListParams');
    }

    logger.info('OpenChannel: Fetching messages', { currentOpenChannel, messageListParams });

    currentOpenChannel.getMessagesByTimestamp(
      lastMessageTimestamp || new Date().getTime(),
      messageListParams,
      (messages, error) => {
        if (!error) {
          logger.info('OpenChannel | Fetching messages succeeded', messages);
          const hasMore = (messages && messages.length > 0);
          const lastMessageTimestamp = hasMore
            ? messages[0].createdAt
            : null;
          messagesDispatcher({
            type: messageActionTypes.GET_PREV_MESSAGES_SUCESS,
            payload: {
              currentOpenChannel,
              messages,
              hasMore,
              lastMessageTimestamp,
            },
          });
          setTimeout(() => {
            callback();
          });
        } else {
          logger.error('OpenChannel | Fetching messages failed', error);
          messagesDispatcher({
            type: messageActionTypes.GET_PREV_MESSAGES_FAIL,
            payload: {
              currentOpenChannel,
              messages: [],
              hasMore: false,
              lastMessageTimestamp: 0,
            },
          });
        }
      }
    );
  }, [currentOpenChannel, lastMessageTimestamp]);

  // useCheckIsScrollBottom
  const checkScrollBottom = () => {
    let isBottom = true;
    try {
      const conversationScroll = conversationScrollRef.current;
      isBottom = conversationScroll.scrollHeight <= conversationScroll.scrollTop + conversationScroll.clientHeight;
    } catch (error) {
    }
    return isBottom;
  };

  // useSendMessageCallback
  const handleSendMessage = useCallback(() => {
    const text = messageInputRef.current.value;

    function createParamsDefualt(txt: string | number): Sendbird.UserMessageParams {
      const message = typeof txt === 'string' ? txt.trim() : txt.toString(10).trim();
      const params = new sdk.UserMessageParams();
      params.message = message;
      return params;
    }

    const createCustomParams = onBeforeSendUserMessage
      && typeof onBeforeSendUserMessage === 'function';

    if (createCustomParams) {
      logger.info('OpenChannel | Creating params using onBeforeSendUserMessage', onBeforeSendUserMessage);
    }

    const params = onBeforeSendUserMessage
      ? onBeforeSendUserMessage(text)
      : createParamsDefualt(text);

    logger.info('OpenChannel | Sending message has started', params);

    const isBottom = checkScrollBottom();
    const pendingMessage = currentOpenChannel.sendUserMessage(params, (message, error) => {
      if (error) {
        logger.warning('OpenChannel | Sending message failed', { message });
        messagesDispatcher({
          type: messageActionTypes.SENDING_MESSAGE_FAILED,
          payload: message,
        });
        return;
      }
      logger.info('OpenChannel | Sending message succeeded', { message });
      messagesDispatcher({
        type: messageActionTypes.SENDING_MESSAGE_SUCCEEDED,
        payload: message,
      });
      if (isBottom) {
        setTimeout(() => {
          utils.scrollIntoLast('.sendbird-msg--scroll-ref');
        });
      }
    });
    messagesDispatcher({
      type: messageActionTypes.SENDING_MESSAGE_START,
      payload: {
        message: pendingMessage,
        channel: currentOpenChannel,
      },
    });
  }, [currentOpenChannel, onBeforeSendUserMessage]);

  // useFileUploadCallback
  const handleFileUpload = useCallback((file) => {
    function createParamsDefualt(file_: File): Sendbird.FileMessageParams {
      const params = new sdk.FileMessageParams();
      params.file = file_;
      return params;
    }

    const createCustomParams = onBeforeSendFileMessage
      && typeof onBeforeSendFileMessage === 'function';

    if (createCustomParams) {
      logger.info('OpenChannel | Creating params using onBeforeSendFileMessage', onBeforeSendFileMessage);
    }

    const params = onBeforeSendFileMessage
      ? onBeforeSendFileMessage(file)
      : createParamsDefualt(file);

    logger.info('OpenChannel | Uploading file message start', params);

    const isBottom = checkScrollBottom();
    const pendingMessage = currentOpenChannel.sendFileMessage(
      params,
      (message: EveryMessage, error: Sendbird.SendBirdError) => {
        if (error) {
          logger.error('OpenChannel | Sending file message failed', { message, error });
          message.localUrl = URL.createObjectURL(file);
          message.file = file;
          messagesDispatcher({
            type: messageActionTypes.SENDING_MESSAGE_FAILED,
            payload: message,
          });
          return;
        }
        logger.info('OpenChannel | Sending message succeeded', message);
        messagesDispatcher({
          type: messageActionTypes.SENDING_MESSAGE_SUCCEEDED,
          payload: message,
        });
        if (isBottom) {
          setTimeout(() => {
            utils.scrollIntoLast('.sendbird-msg--scroll-ref');
          });
        }
      }
    );

    messagesDispatcher({
      type: messageActionTypes.SENDING_MESSAGE_START,
      payload: {
        message: {
          ...pendingMessage,
          url: URL.createObjectURL(file),
          // pending thumbnail message seems to be failed
          requestState: 'pending',
        },
        channel: currentOpenChannel,
      }
    });
  }, [currentOpenChannel, onBeforeSendFileMessage]);

  // useUpdateMessageCallback
  const updateMessage = useCallback((messageId: number, text: string, callback?: () => void): void => {

    function createParamsDefualt(txt: string): SendBird.UserMessageParams {
      const params = new sdk.UserMessageParams();
      params.message = txt;
      return params;
    }

    if (onBeforeSendUserMessage && typeof onBeforeSendUserMessage === 'function') {
      logger.info('OpenChannel | Creating params using onBeforeUpdateUserMessage');
    }

    const params = onBeforeSendUserMessage
      ? onBeforeSendUserMessage(text)
      : createParamsDefualt(text);

    currentOpenChannel.updateUserMessage(messageId, params, (message, error) => {
      if (callback) {
        callback();
      }
      if (!error) {
        logger.info('OpenChannel | Updating message succeeded', { message, params });
        messagesDispatcher({
          type: messageActionTypes.ON_MESSAGE_UPDATED,
          payload: {
            channel: currentOpenChannel,
            message,
          },
        });
      } else {
        logger.warning('OpenChannel | Updating message failed', error);
      }
    });
  }, [currentOpenChannel, messagesDispatcher, onBeforeSendUserMessage]);

  // useDeleteMessageCallback
  const deleteMessage = useCallback((message: ClientUserMessage | ClientFileMessage, callback?: () => void): void => {
    logger.info('OpenChannel | useDeleteMessageCallback: Deleting message', message);
    const { requestState } = message;
    logger.info('OpenChannel | useDeleteMessageCallback: Deleting message requestState: ', requestState);

    if (requestState === 'failed' || requestState === 'pending') {
      logger.info('OpenChannel | useDeleteMessageCallback: Deleted message from local: ', message);
      messagesDispatcher({
        type: messageActionTypes.ON_MESSAGE_DELETED_BY_REQ_ID,
        payload: message.reqId,
      });
      if (callback) {
        callback();
      }
      return;
    }

    currentOpenChannel.deleteMessage(message, (error) => {
      logger.info('OpenChannel | useDeleteMessageCallback: Deleting message from remote: ', requestState);
      if (callback) {
        callback();
      }
      if (!error) {
        logger.info('OpenChannel | useDeleteMessageCallback: Deleting message succeeded', message);
        messagesDispatcher({
          type: messageActionTypes.ON_MESSAGE_DELETED,
          payload: {
            channel: currentOpenChannel,
            messageId: message.messageId,
          },
        });
      } else {
        logger.warning('OpenChannel | useDeleteMessageCallback: Deleting message failed', error);
      }
    });
  }, [currentOpenChannel, messagesDispatcher]);

  // useResendMessageCallback
  const resendMessage = useCallback((failedMessage: ClientUserMessage | ClientFileMessage) => {
    logger.info('OpenChannel | Reseding message has started', failedMessage);
    const { messageType, file } = failedMessage;
    if (failedMessage && typeof failedMessage.isResendable === 'function' && failedMessage.isResendable()) {
      // eslint-disable-next-line no-param-reassign
      failedMessage.requestState = 'pending';
      messagesDispatcher({
        type: messageActionTypes.RESENDING_MESSAGE_START,
        payload: {
          channel: currentOpenChannel,
          message: failedMessage,
        },
      });

      // userMessage
      if (messageType === 'user' && failedMessage.messageType === 'user') {
        currentOpenChannel.resendUserMessage(failedMessage, (message, error) => {
          if (!error) {
            logger.info('OpenChannel | Reseding message succeeded', message);
            messagesDispatcher({
              type: messageActionTypes.SENDING_MESSAGE_SUCCEEDED,
              payload: message,
            });
          } else {
            logger.warning('OpenChannel | Resending message failed', error);
            // eslint-disable-next-line no-param-reassign
            failedMessage.requestState = 'failed';
            messagesDispatcher({
              type: messageActionTypes.SENDING_MESSAGE_FAILED,
              payload: failedMessage,
            });
          }
        });
        return;
      }

      // fileMessage
      if (messageType === 'file' && failedMessage.messageType === 'file') {
        currentOpenChannel.resendFileMessage(failedMessage, file, (message, error) => {
          if (!error) {
            logger.info('OpenChannel | Resending file message succeeded', message);
            messagesDispatcher({
              type: messageActionTypes.SENDING_MESSAGE_SUCCEEDED,
              payload: message,
            });
          } else {
            logger.warning('OpenChannel | Resending file message failed', error);
            // eslint-disable-next-line no-param-reassign
            failedMessage.requestState = 'failed';
            messagesDispatcher({
              type: messageActionTypes.SENDING_MESSAGE_FAILED,
              payload: failedMessage,
            });
          }
        });
      }
    } else {
      // to alert user on console
      // eslint-disable-next-line no-console
      console.error('OpenChannel | Message is not resendable');
      logger.warning('OpenChannel | Message is not resendable', failedMessage);
    }
  }, [currentOpenChannel, messagesDispatcher]);

  if (
    !currentOpenChannel
    || !currentOpenChannel.url
    || amIBanned
  ) {
    return (<div className={COMPONENT_CLASS_NAME}><PlaceHolder type={PlaceHolderTypes.NO_CHANNELS} /></div>);
  }
  if (loading) {
    return (<div className={COMPONENT_CLASS_NAME}><PlaceHolder type={PlaceHolderTypes.LOADING} /></div>);
  }
  if (isInvalid) {
    return (<div className={COMPONENT_CLASS_NAME}><PlaceHolder type={PlaceHolderTypes.WRONG} /></div>);
  }

  return (
    <UserProfileProvider
      className={COMPONENT_CLASS_NAME}
      disableUserProfile={userDefinedDisableUserProfile}
      renderUserProfile={userDefinedRenderProfile}
    >
      {
        renderChannelTitle
          ? renderChannelTitle({
            channel: currentOpenChannel,
            user: user,
          })
          : (
            <OpenchannelConversationHeader
              title={currentOpenChannel.name}
              subTitle={`${utils.kFormatter(currentOpenChannel.participantCount)} ${stringSet.OPEN_CHANNEL_CONVERSATION__TITLE_PARTICIPANTS}`}
              coverImage={currentOpenChannel.coverUrl}
              onActionClick={onChatHeaderActionClick}
              amIOperator={amIOperator}
            />
          )
      }
      {
        currentOpenChannel.isFrozen && (
          <FrozenChannelNotification />
        )
      }
      <OpenchannelConversationScroll
        ref={conversationScrollRef}
        renderCustomMessage={renderCustomMessage}
        openchannel={currentOpenChannel}
        user={user}
        useMessageGrouping={useMessageGrouping}
        isOnline={isOnline}
        allMessages={allMessages}
        onScroll={onScroll}
        hasMore={hasMore}
        updateMessage={updateMessage}
        deleteMessage={deleteMessage}
        resendMessage={resendMessage}
      />
      {
        renderMessageInput
          ? (
            renderMessageInput({
              channel: currentOpenChannel,
              user: user,
              disabled: disabled,
            })
          )
          : (
            <MessageInputWrapper
              channel={currentOpenChannel}
              user={user}
              ref={messageInputRef}
              disabled={disabled || amIMuted}
              onSendMessage={handleSendMessage}
              onFileUpload={handleFileUpload}
              renderMessageInput={renderMessageInput}
            />
          )
      }
    </UserProfileProvider>
  );
};

export default withSendbirdContext(OpenchannelConversation);
